













































































































































import {Component, Vue} from 'vue-property-decorator';
import BuilderNavbar from "@/components/BuilderUI/navbar/BuilderNavbar.vue";
import ShortcutMenu from "@/components/BuilderUI/shortcutmenu/ShortcutMenu.vue";
import ShortcutContent from "@/components/BuilderUI/shortcutmenu/ShortcutContent.vue";
import {Template} from "@/template/Template";
import {OMS} from "@/OMS";
import {User} from "@/api/auth/User";
import {Category, Request} from "@/api/URI";
import {CRUD} from "@/api/CRUD";

@Component({
  name: "ManageUser",
  components: {ShortcutContent, ShortcutMenu, Navbar: BuilderNavbar},
})

export default class ManageUser extends Vue {

  get activeTemplate(): Template { return OMS.getTemplate() }

  users: Array<User> = [] ;
  selected: User | null = null;

  config = {
    icon: "account",
    title: "ผู้ใช้งานในระบบ"
  };

  isCalling = false;
  isAddModalActive = false;

  newData: object = {
    username: '',
    email: '',
    password: '',
    firstName: '',
    lastName: '',
    role: 'Designer',
    profileImg: ''
  };

  beforeMount() {
    this.getAll();
  }

  async getAll() {
    this.isCalling = true;

    const crud = new CRUD(Category.USER);
    crud.setRequest(Request.GET);
    await crud.shoot({});
    this.users = crud.getResult();
    this.isCalling = crud.status;
  }

  async onCreate() {
    this.isCalling = true;

    const crud = new CRUD(Category.USER);
    crud.setRequest(Request.CREATE);
    crud.setAttachData(this.newData);
    await crud.shoot({});
    this.isCalling = crud.status;

    await this.getAll();
    this.isAddModalActive = false;
  }

  async onEdit() {
    this.isCalling = true;

    const crud = new CRUD(Category.USER);
    crud.setRequest(Request.UPDATE);
    crud.setAttachData(this.selected);
    await crud.shoot({});
    this.isCalling = crud.status;

    await this.getAll();
  }

  async onDelete() {
    this.$buefy.dialog.confirm({
      title: `ยืนยันการระงับบัญชีผู้ใช้งาน`,
      message: `<h4 class="title is-4">ระงับบัญชี ${this.selected?.username}</h4>โดยการกระทำนี้ผู้ใช้นี้จะไม่สามารถเข้าสู่ระบบ รวมถึงกระทำการใด ๆ ต่อที่เกี่ยวกับระบบได้ <br/><br/>สามารถยกเลิกการระงับนี้ได้ที่แท็บ 'ผู้ใช้ทีถูกระงับ'`,
      cancelText: 'กลับ',
      confirmText: `ยืนยันการระงับ ${this.selected?.username}`,
      type: 'is-danger',
      hasIcon: true,
      icon: 'account-cancel',
      iconPack: 'mdi',
      onConfirm: async () => {
        this.isCalling = true;
        const crud = new CRUD(Category.USER);
        crud.setRequest(Request.DELETE);
        crud.setAttachData(this.selected);
        await crud.shoot({});
        this.isCalling = crud.status;

        this.selected = null;
        await this.getAll();
      }
    })
  }

  onClearSelected() {
    this.selected = null;
  }

}

